<template>
  <div class="modal-content">
      <span class="close" v-on:click="exit()">&times;</span>
      <h1>{{ this.title }}</h1>

      <p v-for="p in descriptions" v-bind:key="p"><br>{{ p }}</p>

      <a v-if="gh.length > 0" v-bind:href="gh" target="_blank" ><div class="button"><img src="../assets/github.png"/>GitHub</div></a>
      <a v-if="dp.length > 0" v-bind:href="dp" target="_blank" ><div class="button"><img src="../assets/devpost.png"/>Devpost</div></a>
      
  </div>
</template>

<script>

export default {
    name: "Modal",
    props: {
        title: String, descriptions: Array, index: Number, gh: String, dp: String
        
    },

    methods: {
        exit() {
            this.$parent.exit();
        }
    }
}
</script>

