.modal-content {
  background-color: #fefefe;
  padding: 35px;
  width: calc(260px + (450 - 270) * ((100vw - 300px) / (1600 - 300)));
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  border-radius: 7px;
  
}

.close {
  color: #aaa;
  float: right;
  font-size: 32px;
  font-weight: bold;
  margin: -30px -20px;
}

.close:hover,
.close:focus {
  color: red;
  text-decoration: none;
  cursor: pointer;
}

h1 {
    text-align: left;
}

p {
    text-align: left;
    
}

.button {
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 0;
    background-color: #dddddd;
    margin: 20px 20px 0px 0px;
    padding: 7px 12px;
    float: left;
    text-decoration: none;
    color: black;
    
    
}

img {
    height: 16px;
    margin-right: 5px;
    bottom: -2px;
